import React, { Component } from 'react';
import classnames from 'classnames';

import {
  WHITE,
  BLACK,
  LENGTH,
  WIN,
  directions,
  directionsOffset,
} from 'js/common/constant';

import DomFive from 'js/components/DomFive';
import CanvasFive from 'js/components/CanvasFive';

const getIndex = (position = {}) => {
  const { x, y } = position;
  const index = x + y * LENGTH;

  return index;
};

const validate = num => num >= 0 && num < LENGTH;
const validatePosition = position => validate(position.x) && validate(position.y);
const validateDirection = (index, direction) => {
  const baseX = index % LENGTH;
  const baseY = Math.floor(index / LENGTH);

  const directionIndex = directions.indexOf(direction);
  const directionOffset = directionsOffset[directionIndex];

  const position = {
    x: baseX + directionOffset.offsetX,
    y: baseY + directionOffset.offsetY,
  };

  return validatePosition(position);
};

const calcAmount = (store = []) => {
  const loop = (currIndex, amount = 0, directionIndex) => {
    if (currIndex === undefined) {
      return 0;
    }

    const amountList = [amount];
    const valueInStore = store[currIndex].filter(
      (a, index) => directionIndex === undefined || directionIndex === index
    );

    for (let v = 0; v < valueInStore.length; v += 1) {
      const value = valueInStore[v];
      const nextDirectionIndex = directionIndex || v;

      if (!value) {
        continue;
      }

      const nextCurrIndex = currIndex + directions[nextDirectionIndex];

      valueInStore[v] = 0;
      amountList[v] = loop(nextCurrIndex, amount + 1, nextDirectionIndex);

      if (amount >= WIN) {
        return amount;
      }
    }

    return Math.max.apply(null, amountList.filter(a => a !== undefined));
  };

  return loop;
};

const baseState = {
  curr: 0, // 当前历史记录的位置
  history: [], // 历史记录
  winner: '',
};

class Test extends Component {
  state = {
    dom: false,
    ...baseState,
  };

  onMove = (position = {}) => {
    const { history = [], curr, winner } = this.state;

    if (winner) {
      return;
    }

    const baseHistory = history.slice(0, curr);
    const index = getIndex(position);

    if (this._isInHistory(index)) {
      return;
    }

    const newHistory = [...baseHistory, index];

    this.setState({
      history: newHistory,
      curr: newHistory.length,
    }, () => this._judge());
  }

  onClickRestart = () => {
    this.setState(baseState);
  }

  onClickSwitch = () => {
    const { dom } = this.state;

    this.setState({
      dom: !dom,
    });
  }

  // 当前位置是否已经落子
  _isInHistory(index) {
    const { history = [], curr } = this.state;
    const baseHistory = history.slice(0, curr);

    return baseHistory.some(item => item === index);
  }

  _judgePieces(pieces = [], winner) {
    // 根据位置排序
    pieces = pieces
      .slice()
      .sort((a, b) => a - b);

    let store = pieces.reduce((a, b) => {
      a[b] = true;

      return a;
    }, []);

    store = store.reduce((a, b, index) => {
      if (b) {
        a[index] = directions.map((direction) => {
          if (!validateDirection(index, direction)) {
            return 0;
          }

          const indexInStore = direction + index;
          const valueInStore = store[indexInStore];

          return valueInStore ? 1 : 0;
        });
      }

      return a;
    }, []);

    const calcAmountInStore = calcAmount(store);

    for (let v = 0; v < pieces.length; v += 1) {
      const currIndex = pieces[v];

      const amount = calcAmountInStore(currIndex, 0);

      if (amount >= WIN) {
        this.setState({
          winner,
        });

        break;
      }
    }
  }

  // 判断胜负
  _judge() {
    const { history = [], curr = 0 } = this.state;

    const blackPieces = history
      .slice(0, curr)
      .filter((item, index) => !(index % 2));

    const whitePieces = history
      .slice(0, curr)
      .filter((item, index) => index % 2);

    this._judgePieces(blackPieces, BLACK);
    this._judgePieces(whitePieces, WHITE);
  }

  // 跳转至指定历史记录
  _go(num) {
    const { history = [], curr = 0 } = this.state;
    const { length = 0 } = history;

    const baseCurr = curr + num;
    const newCurr = Math.min(Math.max(baseCurr, 0), length);

    this.setState({
      curr: newCurr,
      winner: '',
    }, () => this._judge());
  }

  renderBtns() {
    const { dom } = this.state;

    const title = `切换至${dom ? 'Cavnas' : 'Dom'}`;

    return (
      <div className="btns">
        <div className="btn" onClick={() => this._go(-1)}>悔棋</div>
        <div className="btn" onClick={() => this._go(1)}>撤销悔棋</div>
        <div className="btn" onClick={this.onClickRestart}>重启</div>
        <div className="btn" onClick={this.onClickSwitch}>{ title }</div>
      </div>
    );
  }

  renderTitle() {
    const { winner = '' } = this.state;

    if (!winner) {
      return null;
    }

    const isBlack = winner === BLACK;
    const title = `${ isBlack ? '黑棋' : '白棋' }`;
    const cls = classnames({
      'test-title': true,
      active: isBlack,
    });

    return (
      <div className={cls} data-before={title}>
        获胜，点击重启可重新开始
      </div>
    );
  }

  renderComp() {
    const { dom, history = [], curr } = this.state;

    const Comp = dom ? DomFive : CanvasFive;
    const fiveData = history.slice(0, curr);

    return (
      <Comp
        className="test-container"
        length={LENGTH}
        data={fiveData}
        onMove={this.onMove}
        />
    );
  }

  render() {
    return (
      <div className="page-home-test-render">
        { this.renderBtns() }
        { this.renderTitle() }
        { this.renderComp() }
      </div>
    );
  }
}

export default Test;
