import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import { BLACK, WHITE, LENGTH, WIDTH } from 'js/common/constant';

class DomFive extends Component {
  static propTypes = {
    length: PropTypes.number,
    onMove: PropTypes.func,
  };

  static defaultProps = {
    data: [],
    length: LENGTH,
    onMove: undefined,
  };

  state = {};

  onClick = (e) => {
    const { onMove } = this.props;

    const { currentTarget, clientX, clientY } = e;
    const rect = currentTarget.getBoundingClientRect();

    const x = Math.floor((clientX - rect.x) / WIDTH);
    const y = Math.floor((clientY - rect.y) / WIDTH);

    onMove && onMove({ x, y });
  }

  _getContainerStyle() {
    const { length } = this.props;

    const style = {
      width: `${length * WIDTH}px`,
      height: `${length * WIDTH}px`,
    };

    return style;
  }

  renderPieces() {
    const {
      data = [],
    } = this.props;

    const style = this._getContainerStyle();
    const items = data.map((item, index) => {
      const x = item % LENGTH;
      const y = Math.floor(item / LENGTH);

      const cls = classnames({
        item: true,
        active: (index + 1) % 2,
      });

      const itemStyle = {
        transform: `translate(${x * WIDTH + 1}px, ${y * WIDTH + 1}px)`,
      };

      return (
        <div
          key={index}
          className={cls}
          style={itemStyle}
          />
      );
    });

    return (
      <div className="five-container" style={style} onClick={this.onClick}>
        { items }
      </div>
    );
  }

  render() {
    const { className } = this.props;

    const cls = classnames({
      'components-dom-five-render': true,
      [className]: !!className,
    });

    return (
      <div className={cls}>
        { this.renderPieces() }
      </div>
    );
  }
}

export default DomFive;
