import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import { BLACK, WHITE, LENGTH, WIDTH } from 'js/common/constant';

const BG_COLOR = '#fff';
const LINE_COLOR = '#aaa';
const PIECE_LINE_COLOR = '#aaa';
const WHITE_PIECE_COLOR = '#fff';
const BLACK_PIECE_COLOR = '#000';
const PIECE_WIDTH = 47;

class CanvasFive extends Component {
  constructor(props) {
    super(props);

    this.canvasRef = createRef();
    this.ctx = null;
    this.canvasWidth = 0;
    this.canvasHeight = 0;
    this.ratio = 1;
  }

  static propTypes = {
    length: PropTypes.number,
    onMove: PropTypes.func,
  };

  static defaultProps = {
    data: [],
    length: LENGTH,
    onMove: undefined,
  };

  state = {};

  componentDidMount() {
    this._paint();
  }

  componentDidUpdate() {
    this._paint()
  }

  onClick = (e) => {
    const { onMove } = this.props;

    const { currentTarget, clientX, clientY } = e;
    const rect = currentTarget.getBoundingClientRect();

    const x = Math.floor((clientX - rect.x) / WIDTH);
    const y = Math.floor((clientY - rect.y) / WIDTH);

    onMove && onMove({ x, y });
  }

  _getContext() {
    const { length } = this.props;
    const cva = this.canvasRef.current;

    if (!cva) {
      return null;
    }

    if (this.ctx) {
      return this.ctx;
    }

    const ratio = window.devicePixelRatio;
    const ctx = cva.getContext('2d');

    const base = length * WIDTH;

    this.ctx = ctx;
    this.canvasWidth = base * ratio;
    this.canvasHeight = base * ratio;
    this.ratio = ratio;

    cva.setAttribute('width', `${this.canvasWidth}px`);
    cva.setAttribute('height', `${this.canvasHeight}px`);

    cva.style.width = `${base}px`;
    cva.style.height = `${base}px`;

    return ctx;
  }

  _setConstant() {
    const ctx = this._getContext();

    if (!ctx) {
      return;
    }

    // 画白棋
    ctx.strokeStyle = PIECE_LINE_COLOR;
    ctx.fillStyle = WHITE_PIECE_COLOR;
    ctx.save();

    // 画黑棋
    ctx.fillStyle = BLACK_PIECE_COLOR;
    ctx.save();

    // 画格子
    ctx.lineWidth = 1;
    ctx.strokeStyle = LINE_COLOR;
    ctx.save();

    // 清除 Cavnas
    ctx.fillStyle = BG_COLOR;
    ctx.save();
  }

  _clearRect() {
    const ctx = this._getContext();

    if (!ctx) {
      return;
    }

    const { canvasWidth, canvasHeight } = this;

    ctx.restore();
    ctx.fillRect(0, 0, canvasWidth, canvasHeight);
  }

  _paintLines() {
    const { length } = this.props;
    const ctx = this._getContext();

    if (!ctx) {
      return;
    }

    const { canvasWidth, canvasHeight } = this;
    const points = [];

    for (let v = 0; v <= length; v += 1) {
      const numWidth = canvasWidth * v / length;
      const numHeight = canvasHeight * v / length;

      // 横线
      points.push({
        begin: [0, numHeight],
        end: [canvasWidth, numHeight],
      });

      // 竖线
      points.push({
        begin: [numWidth, 0],
        end: [numWidth, canvasWidth],
      });
    }

    ctx.restore();

    ctx.beginPath();
    points.forEach((point = {}) => {
      ctx.moveTo(...point.begin);
      ctx.lineTo(...point.end);
    });
    ctx.stroke();
  }

  _paintPieces() {
    const { data = [], length } = this.props;
    const ctx = this._getContext();

    if (!ctx) {
      return;
    }

    const { ratio, canvasWidth, canvasHeight } = this;
    const r = PIECE_WIDTH * ratio / 2;

    const blackPieces = data.filter((a, index) => !(index % 2));
    const whitePieces = data.filter((a, index) => !!(index % 2));

    ctx.restore();

    blackPieces.forEach((item) => {
      const x = item % length;
      const y = Math.floor(item / length);

      const positionX = canvasWidth * (x + 0.5) / length;
      const positionY = canvasHeight * (y + 0.5) / length;

      ctx.beginPath();
      ctx.arc(positionX, positionY, r, 0, Math.PI * 2);
      ctx.fill();
    });

    ctx.restore();

    whitePieces.forEach((item) => {
      const x = item % length;
      const y = Math.floor(item / length);

      const positionX = canvasWidth * (x + 0.5) / length;
      const positionY = canvasHeight * (y + 0.5) / length;

      ctx.beginPath();
      ctx.arc(positionX, positionY, r, 0, Math.PI * 2);
      ctx.fill();
      ctx.stroke();
    });
  }

  _paint() {
    this._setConstant();
    this._clearRect();
    this._paintLines();
    this._paintPieces();
  }

  renderCanvas() {
    return (
      <canvas
        className="five-canvas"
        ref={this.canvasRef}
        onClick={this.onClick}
        />
    );
  }

  render() {
    const { className } = this.props;

    const cls = classnames({
      'components-canvas-five-render': true,
      [className]: !!className,
    });

    return (
      <div className={cls}>
        { this.renderCanvas() }
      </div>
    );
  }
}

export default CanvasFive;
